<template>
  <div class="editDialog">
    <editForm
      :formConfig="formConfig"
      ref="addeditForm"
      :formData="editFormData"
    >
    </editForm>
  </div>
</template>

<script>
export default {
  props: {
    formData: {
      type: Object,
      default: {}
    },
    selectUserOption:{
      type: Array,
      default: []
    },
  },
  name: "editDialog",
  data() {
    return {
      // orgid: '',
      editFormData: {},
      formConfig: {
        itemArr: [
          {
            type: "text",
            label: "资产分类",
            prop: "assetTypeName",
            id: "assetTypeId",
            span: 24,
            required: true,
            disabled: true
          },
          {
            label: "资产型号",
            prop: "goodsModel",
            required: true,
            disabled: true,
            type: "text",
            span: 24
          },
          {
            label: "分配日期",
            prop: "billDate",
            type: "date",
            required: true,
            span: 24
          },
          // {
          //   label: "归属人",
          //   prop: "staffunName",
          //   idsStr: "empId",
          //   type: "user",
          //   trigger: "change",
          //   required: true,
          //   span: 24
          // },
          {
            label: "归属人",
            prop: "staffunName",
            type: "autocomplete",
            // idsStr: "empId",
            trigger: "change",
            placeholder: "可输入关键词查找归属人",
            required: true,
            seleteOption: this.selectUserOption,
            selectMethods: (obj) => {
              this.$refs.addeditForm.setFormItem("empId", obj.id);
            },
            span: 24,
          },
          {
            label: "备注",
            prop: "remark",
            type: "textarea",
            span: 24,
            rows: 4
          }
          // {
          //   label: "分配数量",
          //   prop: "portionQty",
          //   type: "el-number",
          //   required: true,
          //   min: 0,
          //   max: this.maxNum,
          //   precision: 0,
          //   step: 1,
          //   span: 24,
          //   // required: true,
          //   // ruleArr: [{ max: 100, message: "最多100个字符", trigger: "blur" }],
          // },
        ]
      }
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.editFormData = Object.assign({}, this.formData);
    },
    // 获取表单数据
    getFormData() {
      let This = this;
      return new Promise((resolve, reject) => {
        this.$refs.addeditForm
          .getFormData()
          .then(data => {
            data = Object.assign({}, data);
            resolve(data);
          })
          .catch(e => {
            reject(e);
          });
      });
    }
  }
};
</script>

