<template>
  <div class="editDialog">
    <editForm :formConfig="formConfig" ref="editForm" :formData="editFormData">
    </editForm>
  </div>
</template>

<script>
export default {
  props: {
    formData: {
      type: Object,
      default: {},
    },
  },
  name: "editDialog",
  data() {
    return {
      // orgid: '',
      editFormData: {},
      formConfig: {
        itemArr: [
          {
            type: "select",
            label: "资产分类",
            prop: "assetTypeName",
            id: "assetTypeId",
            required: true,
            disabled: true,
            seleteOption: this.$store.getters.assetTypeList,
            span: 24,
          },
          {
            label: "分配型号",
            prop: "goodsModel",
            required: true,
            disabled: true,
            type: "text",
            span: 24,
            // ruleArr: [{ max: 100, message: "最多100个字符", trigger: "blur" }],
          },
          //    {
          //       label: "采购数量",
          //       prop: "qty",
          //       required: true,
          //       disabled: true,
          //       type: "number",
          //       span: 24
          //     },
          {
            label: "分配日期",
            prop: "billDate",
            type: "date",
            required: true,
            span: 24,
          },
          {
            label: "分配部门",
            prop: "depatName",
            idsStr: "depatCode",
            type: "dept",
            disabled: true,
            trigger: "change",
            required: true,
            span: 24,
          },
          {
            label: "分配数量",
            prop: "portionQty",
            type: "el-number",
            required: true,
            min: 0,
            max: this.maxNum,
            precision: 0,
            step: 1,
            span: 24,
            // required: true,
            // ruleArr: [{ max: 100, message: "最多100个字符", trigger: "blur" }],
          },
        ],
      },
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.editFormData = Object.assign({}, this.formData);
    },
    // 获取表单数据
    getFormData() {
      let This = this;
      return new Promise((resolve, reject) => {
        this.$refs.editForm
          .getFormData()
          .then((data) => {
            data = Object.assign({}, data);
            resolve(data);
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
  },
};
</script>

