import request from '@/request/api'
/**===========基本信息接口 */
//获取资产分配的列表
export function getListApi(data = {}) {
    return request({
        url: '/asset/assetsPortionAction/queryList',
        method: 'post',
        data: data
    })
}

//资产分配-编辑
export function updatePortion(data = {}) {
    return request({
        url: '/asset/assetsPortionAction/updatePortion',
        method: 'post',
        data: data
    })
}

//资产分配-详情列表
export function getDetailApi(data = {}) {
    return request({
        url: '/asset/assetsPortionDetAction/queryList',
        method: 'post',
        data: data
    })
}

//资产分配-删除
export function deletePortion(id = "") {
    return request({
        url: '/asset/assetsPortionAction/deletePortion',
        method: 'post',
        data: { id: id }
    })
}

// 资产分配详情-删除
export function deletePortionDet(id = "") {
    return request({
        url: '/asset/assetsPortionDetAction/deletePortionDet',
        method: 'post',
        data: { id: id }
    })
}
//资产分配明细-添加
export function addPortionDet(data = {}) {
    return request({
        url: '/asset/assetsPortionDetAction/addPortionDet',
        method: 'post',
        data: data
    })
}
//资产分配明细-修改
export function updatePortionDet(data = {}) {
    return request({
        url: '/asset/assetsPortionDetAction/updatePortionDet',
        method: 'post',
        data: data
    })
}



