<template>
  <div class="assetRegister h100">
    <!-- <publicCrumbs :crumbsData="crumbsData"></publicCrumbs> -->
    <div class="page-main-box">
      <KlTable
        v-bind="tableConfig"
        v-if="pageType == 'list'"
        ref="klTable"
      ></KlTable>
      
      <editPage
        v-if="pageType !== 'list'"
        @goBackPage="goBackPage"
        :rightBtnArr="rightBtnArr"
      >
        <!-- <addInfo
          ref="editForm"
          v-if="pageType == 'add'"
          :pageType="pageType"
          :formData="formData"
        ></addInfo> -->

        <!-- 资产分配-点击详情操作按钮，跳转去详情页面 -->
        <detailsInfo
          ref="details"
          key="details"
          v-if="pageType == 'details'"
          :formData="formData"
        ></detailsInfo>
      </editPage>

      <!-- 资产分配-部门弹窗 -->
      <dialogPublic
        title="部门"
        :show.sync="showMember"
        @doSubmit="chooseUser"
        @doClose="() => (showMember = false)"
        appendTobody
        width="800px"
        height="400px"
      >
        <orgMemberSelect
          ref="orgMemberSelect"
          v-if="showMember"
          :seleteSetting="orgMemberSelect"
          :otherInfo="otherInfo"
        ></orgMemberSelect>
      </dialogPublic>

      <!-- 资产分配-编辑弹窗 -->
      <dialogPublic
        :title="'资产分配-编辑 / 数量：' + formData.portionQty"
        :show.sync="editDialog"
        @doSubmit="editSubmit"
        @doClose="() => (editDialog = false)"
        appendTobody
        width="800px"
        height="400px"
      >
        <editDialog
          v-if="editDialog"
          :formData="formData"
          ref="distributionFrom"
        >
        </editDialog>
      </dialogPublic>
    </div>
  </div>
</template>

<script>
// import addInfo from "./add"; //新建页面
import editDialog from "./editDialog"; //编辑弹窗
import detailsInfo from "./details"; //详情页面

import {
  getListApi, //获取分配页面列表
  updatePortion, //编辑分配
  getDetailApi, //详情列表
  deletePortion, //删除按钮
} from "@/api/itAssets/assetAllocation";
// import { queryAssetTypeList } from "@/api/common";

export default {
  name: "assetAllocation",
  components: { editDialog, detailsInfo },
  data() {
    return {
      label: "",
      otherInfo: "",
      showMember: false,
      details: false,
      editDialog: false,
      currentItem: {},
      orgMemberSelect: {},

      //   crumbsData: {
      //     //面包屑
      //     titleList: [
      //       { title: MainTitle },
      //       { title: SubTitle, path: `/${MainPath}/${SubPath}` },
      //     ],
      //   },
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-input",
              label: "型号",
              model: "goodsModel",
              placeholder: "模糊查询",
            },
            {
              type: "el-select",
              label: "资产分类",
              model: "assetTypeId",
              option: this.$store.getters.assetTypeList,
            },
            {
              type: "el-input",
              label: "部门",
              model: "depatName",
              // id: "depatCode",
              // scopeType: "formate",
              method: (row) => {
                this.chooseStaff("dept");
              },
              placeholder: "部门",
            },
          ],
        },

        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageSize: 15 //pageCount
          }
        },

        tableDataObj: {
          tableArr: [],
          total: null,
        },

        operateArr: [
          // {
          //   name: "新建",
          //   // isHide: !this.$store.getters["isBtnAuth"]("insert"),
          //   icon: "el-icon-plus",
          //   method: (name) => {
          //     this.toAdd();
          //   },
          // },
        ],

        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,

          // 左边序号展示
          indexShow: true,

          // table表事件参数配置
          tableMethods: {
            selection: (val) => {
              this.selectionData = val;
            },
          },
          /* selectableMethod(row, index) {
            let value = row.isAffirm;
            return value == 0 ? true : value == 1 ? false : true;
          }, */

          // table表展示列配置
          showItems: [
            {
              name: "单据编号",
              prop: "portionBillNo",
              width: 100,
              // align: "left",
            },
            {
              name: "资产分类",
              prop: "assetTypeName",
              // width: 150,
              // align: "left",
            },

            {
              name: "品牌",
              prop: "brand",
              id: "brand_type",
              width: 50,
              // align: "center",
              // scopeType: "formate",
              // method: (row) => {
              //   let value = row.trainingType;
              //   return this.$store.getters["fineDirtDataName"](
              //     "EDU_TYPE",
              //     value
              //   );
              // },
            },
            {
              name: "型号",
              prop: "goodsModel",
              // width: 150,
              // scopeType: "formate",
              // method: (row) => {
              //   let value = row.trainingPlate;
              //   return this.$store.getters["fineDirtDataName"](
              //     "EDU_SECTION",
              //     value
              //   );
              // },
            },
            {
              name: "部门",
              prop: "depatName",
              width: 120,
            },
            {
              name: "数量",
              prop: "portionQty",
              width: 60,
            },
            {
              name: "已分配数量",
              prop: "overQty",
              width: 60,
            },

            {
              name: "分配日期",
              prop: "billDate",
              width: 100,
            },
            {
              name: "登记合同号",
              prop: "registBillNo",
              width: 120,
            },
            // {
            //   name: "采购日期",
            //   prop: "buyDate",
            //   width: 140
            // },
            // { name: "采购合同号",
            //   prop: "contract_no",
            //   width: 150,
            //   // align: "left"
            // },
            {
              name: "操作",
              prop: "operate",
              width: 150,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                // 最后一行操作栏数组
                {
                  name: "详情",
                  method: (row) => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                    // return this.$store.getters["isBtnAuth"]("details");
                  },
                  icon: "false",
                },

                {
                  name: "编辑",
                  method: (row) => {
                    this.editInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                    // return this.$store.getters["isBtnAuth"]("update");
                  },
                  icon: "false",
                },
                // {
                //   name: "分配",
                //   method: (row) => {
                //     this.editInfo(row);
                //   },
                //   isShow: (row, that) => {
                //     return true;
                //     // return this.$store.getters["isBtnAuth"]("update");
                //   },
                //   icon: "false",
                // },
                {
                  name: "删除",
                  method: (row) => {
                    this.deleteInfo(row);
                  },
                  isShow: (row, that) => {
                    return (row.billDate == '0');
                    // return this.$store.getters["isBtnAuth"]("delete");
                  },
                  icon: "false",
                },
              ],
            },
          ],
        },

        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100],
        },
        paginationIsshow: true,
        showklTable: false,
      },

      pageType: "list",
      formData: {},
    };
  },

  computed: {
    rightBtnArr() {
      // let arr = [
      //   {
      //     text: "保存",
      //     icon: "el-icon-finished",
      //     hide: this.pageType == "details",
      //     methods: () => {
      //       this.doSubmit();
      //     },
      //   },
      // ];
      // return arr;
    },
  },

  watch: {
    pageType(val) {
      if (val === "list") {
        this.getTableData();
      }
    },

    // 部门选择框清空后，显示全部列表
    // 当depatName为空时，清空depatCode，后台返回全部列表数据列表
    "tableConfig.queryObj.queryData.depatName"(val) {
      if (val == "") {
        this.tableConfig.queryObj.queryData.depatCode = "";
      }
    }
  },

  mounted() {
    this.getTableData();
  },

  methods: {
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      // this.$delete(form, "staffunName");//去掉人的中文名
      form.pageCount=form.pageSize;
      this.$delete(form, 'pageSize');
      this.$delete(form, "depatName"); //去掉部门的中文名

      // 型号、资产分类选择框清空后，显示全部列表
      for (const key in form) {
        if (form[key] == "") {
          delete form[key];
        }
      }

      getListApi(form)
        .then((res) => {
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch((res) => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },

    //选择组织人员-部门弹窗
    chooseStaff(type) {
      //对类型进行判断
      let typePre = type.split("-")[0]; //分割type
      let typeBre = type.split("-")[1];
      this.selectType = typePre;
      if (typePre == "dept") {
        //组织选择

        this.orgMemberSelect = {
          seleteType: typeBre ? typeBre : "org",
          radio: typePre == "dept", //单选
          props: {
            label: "organizationName",
            children: "children",
          },
          column: [
            {
              label: "组织",
              prop: "name",
            },
          ],
          chosedData: [],
        };
      } else if (typePre == "user") {
        //人员选择
        this.orgMemberSelect = {
          seleteType: typeBre ? typeBre : "orgmember",
          radio: typePre == "user", //单选
          props: {
            label: "organizationName",
            children: "children",
          },
          column: [
            {
              label: "姓名",
              prop: "name",
              width: 60,
            },
          ],
          chosedData: [],
        };
      }
      this.showMember = true;
    },

    //确定选择人员
    chooseUser() {
      let data = this.$refs.orgMemberSelect.getSeleteData();
      let newData = data[0];
      let names = "";
      let ids = "";
      if (this.selectType == "dept") {
        if (newData) {
          this.$set(
            this.tableConfig.queryObj.queryData,
            "depatName",
            newData.name
          );
          this.$set(
            this.tableConfig.queryObj.queryData,
            "depatCode",
            newData.id
          );
        }
      } else if (this.selectType == "user") {
        if (newData) {
          this.$set(
            this.tableConfig.queryObj.queryData,
            "staffunName",
            newData.name
          );
          this.$set(this.tableConfig.queryObj.queryData, "empId", newData.id);
        }
      }

      this.showMember = false;
    },

    //新建
    // toAdd() {
    //   this.pageType = "add";
    //   this.formData = {};
    // },

    //编辑
    editInfo(row) {
      this.editDialog = true;
      this.formData = Object.assign({}, row);
    },

    //详情
    detailsInfo(row) {
      this.pageType = "details";
      this.formData = Object.assign({}, row);
    },

    //删除
    deleteInfo(row) {
      this.$confirm(`是否确认删除`, "提示", {
        // this.$confirm(`是否删除【${row.trainingName}】`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          deletePortion(row.id).then((res) => {
            this.$message.success("删除操作成功");
            this.getTableData();
          });
        })
        .catch((e) => {});
    },

    // 资产分配-编辑弹框确定按钮
    editSubmit() {
      this.$refs.distributionFrom
        .getFormData()
        .then((data) => {
          this.editDistribution(data).then((res) => {
            this.$message.success("操作成功！");
            this.editDialog = false;
            // this.pageType = "list";
            this.getTableData();
            // this.pageType = "edit";
            // this.$refs.editForm.getDetails(this.formData.id);
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    //编辑分配
    editDistribution(data) {
      return new Promise((resolve, reject) => {
        updatePortion(data)
          .then((res) => {
            resolve(res);
          })
          .catch((e) => {
            console.log(e);
            reject(e);
          });
      });
    },

    //点击提交
    doSubmit() {
      this.$refs.editForm
        .getFormData()
        .then((data) => {
          this.actionMethons(data).then((res) => {
            this.$message.success("操作成功！");
            if (!data.id) {
              this.formData = { id: res.data };
            }

            //  this.pageType = "list";
            //             this.getTableData();

            this.pageType = "edit";
            this.$refs.editForm.getDetails(this.formData.id);
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    actionMethons(data) {
      // return new Promise((resolve, reject) => {
      //   let fileForm = new FormData();
      //   fileForm.append("coverFile", data.coverFile);
      //   delete data.coverFile;
      //   // for (const key in data) {
      //   //   const element = JSON.stringify(data[key]);
      //   //   fileForm.append(key, element);
      //   // }
      //   fileForm.append("datas", JSON.stringify(data));
      //   if (!data.id) {
      //     postNewApi(fileForm)
      //       .then((res) => {
      //         resolve(res);
      //       })
      //       .catch((e) => {
      //         reject(e);
      //       });
      //   } else {
      //     postUpdateApi(fileForm)
      //       .then((res) => {
      //         resolve(res);
      //       })
      //       .catch((e) => {
      //         reject(e);
      //       });
      //   }
      // });
    },
    //返回
    goBackPage(data) {
      this.pageType = "list";
    },
  },
};
</script>

<style>
.page-main-box {
  height: 100%;
  overflow: hidden;
  padding: 10px 20px !important;
  /* height: calc(100% - 20px); */
}
</style>