<template>
  <div class="h100">
    <KlTable v-bind="tableConfig" ref="klTable"></KlTable>
    <!-- 新建分配-编辑弹窗 -->
    <dialogPublic
      :title="title"
      :show.sync="addDialog"
      @doSubmit="editSubmit"
      @doClose="() => (addDialog = false)"
      appendTobody
      width="800px"
      height="400px"
    >
      <addDialog
        v-if="addDialog"
        :formData="addFormData"
        :selectUserOption="selectUserOption"
        ref="distributionFrom"
      >
      </addDialog>
    </dialogPublic>

    
  </div>
</template>

<script>
// import addInfo from "./add";
import addDialog from "./addDialog"; //编辑弹窗
import {
  getDetailApi, // 资产分配详情-列表
  deletePortionDet, // 资产分配详情-删除
  addPortionDet, //资产分配明细-添加
  updatePortionDet //资产分配明细-修改
} from "@/api/itAssets/assetAllocation";

import {
  queryRealNameUser, //查询部门用户
} from "@/api/common";
export default {
  name: "assetAllocation",
  // props:["formData"],
  props: {
    formData: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  components: { addDialog },
  data() {
    return {
      //   crumbsData: {
      //     //面包屑
      //     titleList: [
      //       { title: MainTitle },
      //       { title: SubTitle, path: `/${MainPath}/${SubPath}` },
      //     ],
      //   },
      addDialog: false,
      title:"",
      addFormData: {},
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-input",
              label: "领用人",
              model: "staffunName",
              id:"empId",
              placeholder: "领用人",
              scopeType: "formate",
              method: row => {
                this.chooseStaff("user");
              },
            },
            {
              type: "date",
              label: "分配日期",
              model: "billDate"
            }
          ]
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageSize: 15
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null
        },
        operateArr: [
          {
            name: "新建",
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),remainingNum
            isHide: this.remainingNum,
            icon: "el-icon-plus",
            method: name => {
              this.toAdd();
            }
          }
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: val => {
              this.selectionData = val;
            }
          },
          /* selectableMethod(row, index) {
            let value = row.isAffirm;
            return value == 0 ? true : value == 1 ? false : true;
          }, */
          // table表展示列配置
          showItems: [
            // {
            //   name: "固定资产编码",
            //   prop: "fixedNumber",
            //   width: 150,
            //   // align: "left",
            // },
            {
              name: "资产分类",
              prop: "assetTypeName",
              id: "assetsNumber"
            },
            {
              name: "型号",
              prop: "goodsModel"
            },
            {
              name: "领用人",
              prop: "staffunName"
              // width: 100,
            },
            {
              name: "资产编号",
              prop: "assetsNumber",
              width: 150
              // align: "left",
            },
            // {
            //   name: "归属部门",
            //   prop: "depatName",
            //   // width: 100,
            //   // scopeType: "formate",
            //   // method: (row) => {
            //   //   let val = row.trainingDuration;
            //   //   return val + "分钟";
            //   // },
            // },
            // {
            //   name: "登记合同号",
            //   prop: "registBillNo",
            //   width: 100
            // },
            {
              name: "分配日期",
              prop: "billDate",
              width: 140
            },
            {
              name: "资产状态",
              prop: "assetsStatus",
              scopeType: "formate",
              method: row => {
                let value = row.assetsStatus;
                return (
                  this.$store.getters["fineDirtDataName"](
                    "assets_status",
                    value
                  ) || value
                );
              }
            },
            {
              name: "操作",
              prop: "operate",
              width: 150,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                // 最后一行操作栏数组
                // {
                //   name: "详情",
                //   method: (row) => {
                //     this.detailsInfo(row);
                //   },
                //   isShow: (row, that) => {
                //     return true;
                //     // return this.$store.getters["isBtnAuth"]("details");
                //   },
                //   icon: "false",
                // },
                {
                  name: "编辑",
                  method: row => {
                    this.editInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                    // return this.$store.getters["isBtnAuth"]("update");
                  },
                  icon: "false"
                },
                {
                  name: "删除",
                  method: row => {
                    this.deleteInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                    // return this.$store.getters["isBtnAuth"]("delete");
                  },
                  icon: "false"
                }
              ]
            }
          ]
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        },
        paginationIsshow: true,
        showklTable: false
      },
      pageType: "list",
      selectUserOption:[],
      remainingNum:0,
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.addFormData = {
      assetTypeName: this.formData.assetTypeName,
      assetTypeId: this.formData.assetTypeId,
      goodsModel: this.formData.goodsModel,
      fRef: this.formData.id,
      billDate: this.$utils.getNowDay()
    };
    this.remainingNum = this.formData.portionQty - this.formData.overQty;//剩余资产数量
    this.getUserList();
    this.getTableData();
  },
  methods: {
    getUserList(){
      let form={
        orgNodeCode:this.formData.depatCode,
      }
      queryRealNameUser(form)
        .then(res => {
          let data=res.data || [];
          data.map((i) => (i.value = i.name,i.id = i.code));
          this.selectUserOption = data;

          
        })

    },
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData, {
        fRef: this.formData.id
      });
      form.pageCount=form.pageSize;
      this.$delete(form, 'pageSize');
      this.$delete(form, "staffunName"); //删除人员名字
      getDetailApi(form)
        .then(res => {
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch(res => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },

    //删除
    deleteInfo(row) {
      this.$confirm(`是否确认删除`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deletePortionDet(row.id).then(res => {
            this.$message.success("删除操作成功");
            this.getTableData();
          });
        })
        .catch(e => {});
    },
    //新建按钮
    toAdd() {
      // this.$set(this.addFormData, "assetTypeName", 0);
      this.title = "新建"
      this.addDialog = true;
    },
    //编辑按钮
    editInfo(row) {
      this.title = "编辑"
      this.addDialog = true; //row
      this.addFormData = row;
      // this.$refs.distriburowtionFrom.setTableDate(row);
      // this.$set(this.addFormData, );
    },
    // 新建分配-编辑弹框确定按钮
    editSubmit() {
      this.$refs.distributionFrom
        .getFormData()
        .then(data => {
          this.addDistribution(data).then(res => {
            this.$message.success("操作成功！");
            this.addDialog = false;
            // this.pageType = "list";
            this.getTableData();
            // this.pageType = "edit";
            // this.$refs.editForm.getDetails(this.formData.id);
          });
        })
        .catch(e => {
          console.log(e);
        });
    },
    //分配
    addDistribution(data) {
      this.$delete(data, "assetTypeId");
      if (data.id) {//编辑
        return new Promise((resolve, reject) => {
          updatePortionDet(data)
            .then(res => {
              resolve(res);
            })
            .catch(e => {
              console.log(e);
              reject(e);
            });
        });
      } else {//新建
        return new Promise((resolve, reject) => {
          addPortionDet(data)
            .then(res => {
              resolve(res);
            })
            .catch(e => {
              console.log(e);
              reject(e);
            });
        });
      }
    },


  }
};
</script>

<style>
.el-table__body-wrapper {
  height: 100% !important;
}
</style>